@use "global" as *;

.blog-card {
  color: $background;
  background-color: $white;
  display:flex;
  flex-direction: column;
  transition: .3s;
}

.blog-card:hover {
  color: $white;
  background-color: $background;
}

.blog-card__body {
  color: inherit;
  padding: rem(4) rem(24);
}

.blog-card__title {
  font-size: rem(18);
  font-weight: 500;
  line-height: calc( 35 / 18);
  text-align: center;
  letter-spacing: rem(1);
}

.blog-card__text,
.blog-card__information {
  font-size: rem(14);
  font-weight: $normal;
  line-height: calc( 26 / 14);
  padding-top: rem(4);
}

.blog-card__information {
  display: flex;
  justify-content: space-between;
  margin-top: auto;
  padding: rem(4) rem(24) rem(16);
}

.blog-card__category {
  background-color: $sub-accent-color;
  padding: rem(4) rem(5);
  color: #000;
}



